*{box-sizing:border-box;padding:0;margin:0}html{font-size:62.5%;overflow-x:hidden;scroll-behavior:smooth}body{font-size:1.6rem;font-family:Montserrat,sans-serif;overflow-x:hidden;background:linear-gradient(to top,var(--primary-background-color-gradient),var(--secondary-background-color-gradient)),url(../img/logan-weaver-lgnwvr-yRpe13BHdKw-unsplash.jpg);background-position:top;background-repeat:none;background-size:cover;min-height:100vh}header{color:var(--primary-background-color);position:fixed;top:0;left:0;right:0;z-index:1000}.container{max-width:120rem;margin:0 auto}.header-container{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;padding:0 5rem;height:7rem;max-width:130rem;width:100%;margin:0 auto}.company-name{color:#fff;font-size:2rem}.btn{padding:1rem;border-radius:5px;background-color:var(--primary-background-color);color:#fff;border:none;font-size:1.6rem;font-family:inherit;cursor:pointer;transition:all .3s ease-in-out;width:50%;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center}.btn:hover{transform:scale(1.1)}.btn:active{transform:translateY(2px)}section{display:none}.display--section{display:block}